/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { graphql } from 'gatsby';
import { Box } from '@material-ui/core';
import BioCard from 'components/Cards/BioCard';
import Layout from 'components/Layout';
import SEO from 'components/SEO';
import Posts from 'components/Posts';
import RecentPostsCard from 'components/Cards/RecentPostsCard';
import CategoriesCard from 'src/components/Cards/CategoriesCard';
import Paginator from 'src/components/Paginator';
import { Fragment } from 'react';
import startCase from 'lodash/startCase';

interface Props {
  data: {
    allMarkdownRemark: any;
    site: {
      siteMetadata: {
        title: string;
      };
    };
  };
  location: Location;
}

const CategoryTemplate = ({ data, location }: Props) => {
  const {
    allMarkdownRemark: { edges: posts, pageInfo },
  } = data;
  const [, categoryPath, categorySlug] = location.pathname.split('/');
  const path = `/${categoryPath}/${categorySlug}`;

  return (
    <Layout
      location={location}
      left={
        <Fragment>
          <Box
            css={css`
              margin-bottom: 1rem;
            `}
          >
            <BioCard />
          </Box>
          <Box>
            <CategoriesCard />
          </Box>
        </Fragment>
      }
      right={<RecentPostsCard />}
    >
      <SEO title={startCase(categorySlug)} />
      <Posts posts={posts.map((p: { node: any }) => p.node)} />
      <Paginator pageCount={pageInfo.pageCount} currentPage={pageInfo.currentPage} defaultUrl={path} />
    </Layout>
  );
};

export default CategoryTemplate;

export const pageQuery = graphql`
  query CategoryQuery($skip: Int!, $limit: Int!, $category: String!) {
    allMarkdownRemark(
      skip: $skip
      limit: $limit
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { categories: { in: [$category] } } }
    ) {
      edges {
        node {
          excerpt(pruneLength: 300)
          fields {
            slug
          }
          frontmatter {
            date(formatString: "YYYY-MM-DD")
            title
            path
            categories
            thumbnail {
              childImageSharp {
                fluid(maxWidth: 800) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
      pageInfo {
        currentPage
        pageCount
      }
    }
  }
`;
